body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  /* prevents horizontal scroll */
  box-sizing: border-box;
}

.h1,
h2 {
  display: inline-block;
  margin-left: 36%;
  font-size: 25px;
  border-radius: 10px;
  padding: 5px 20px;
  background-color: #01f715;
}

.form-container {
  display: grid;
  width: 100%;
  padding: 10px 0;
  border-radius: 10px;
}

.form-group {
  flex-direction: column;
  display: inline-block;
  text-align: center;
  margin-left: 40px;
}

.name {
  margin-left: 48px;
}

.email {
  margin-left: 55px;
}

.dob {
  margin-left: 53px;
}

label {
  font-weight: 500;
  font-style: oblique;
  margin-bottom: 6px;
  margin-left: 6px;
  color: #333;
}

input {
  padding: 3px 20px;
  width: 170px;
  margin: 5px 10px;
  border: 1px solid #12e9f8;
  border-radius: 20px;
  text-align: center;
  font-size: 15px;
  background-color: hsl(148, 100%, 97%);
}


/* Table styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 30px;
  background-color: #ffffff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  overflow: hidden;
}

/* Header */
thead {
  background-color: #4fd0f7;
}

th {
  padding: 12px;
  text-align: left;
  font-weight: 750;
  color: #333;
  border-bottom: 2px solid #e0e0e0;
}

.bt-submit {
  margin-top: 10px;
  margin-left: 43%;
  color: #ffffff;
  background-color: #ff00d4;
  border-radius: 20px;
  padding: 6px 12px;
  border: none;
  cursor: pointer;
  font-size: 14px;

}

.bt-submit:hover {
  background-color: #ff0998;
  transition: background-color 0.3s;

}

/* Rows */
td {
  padding: 8px 16px;
  font-weight: 500;
  border-bottom: 1px solid #eee;
  color: #555;
}

tr:hover {
  background-color: #bcc6ff;
  transition: background-color 0.3s;
}

/* Buttons */
.action-buttons button {
  margin-right: 15px;
  padding: 8px 14px;
  border: none;
  color: rgb(247, 244, 244);
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.action-buttons .edit-btn {
  background-color: #1976d2;
}

.action-buttons .edit-btn:hover {
  background-color: #301af7;
}

.action-buttons .delete-btn {
  background-color: #d32f2f;
}

.action-buttons .delete-btn:hover {
  background-color: #ff1313;
}




/* Responsive */
@media (max-width: 768px) {

  table {
    display: block;
    overflow-y: auto;
    width: 350px;
  }

  input {
    display: inline-block;
    width: 175px;
    text-align: center;
    margin: 4px 8px;
    border-radius: 12px;
    background-color: hsla(152, 87%, 91%, 0.322);
    padding-left: 8px;
  }

  /* Adjust button */
  .bt-submit {
    display: block;
    border-radius: 60px;
    margin: 8px 132px;
    width: 100px;
    padding: 5px 10px;
    font-size: 12px;
  }

  .form-container {
    display: grid;
    align-items: center;
    margin-right: 30px;
  }

  .form-group {
    display: inline-block;
    text-align: center;
    margin-left: 25px;
  }

  .email {
    margin-left: 28px;
  }

  .phone {
    margin-left: 22px;
  }

  .age {
    margin-left: 39px;
  }

  .gender {
    margin-left: 18px;
  }

  .address {
    margin-left: 11px;
  }

  .dob {
    margin-left: 40px;
  }

  label {
    font-weight: 500;
    font-style: oblique;
    margin-bottom: 6px;
    margin-left: 20px;
    color: #333;
  }

  h1,
  h2,
  .title {
    font-size: 23px;
    padding: 8px 10px;
    margin-left: 9px;
    background-color: #01f70d;
  }

  /* Table header fix */

  thead{
    width: 290px;
    margin-left: 10px;
    border: 3px solid black;
    border-radius: 10px;
  }

  th {
    font-size: 10px;
    padding: 3px 4px;

  }


  tbody {
    /* display: table; */
    width: 300px;
    font-size: 10px;
    /* border-radius: 30px; */
    /* padding-right: 60px; */
  }

  td{
    font-size: 3px;
    padding: 3px 3px;
  }




  /* Adjust action buttons inside table */
  .action-buttons {
    display: flex;
    flex-direction:column-reverse;
    text-align: end;
    gap: 3px;
  }

  .action-buttons button {
    width: 25px;
    font-size: 3px;
    padding: 2px 3px;
  }
}